import React, { useState, useEffect, useRef, useCallback } from "react";
import { Segmented, Badge, Space, Spin, Empty } from "antd";
import RoomAndDetails from "./roomAndDetails";
import { getAlertList } from "@/api/elderlySupport";
import { getAlertInfoViaEvent, getAlertsGroup } from "@/utils/helper";
import dayjs from "dayjs";

export default function RoomView() {
  const [alertType, setAlertType] = useState("All");
  const [alertList, setAlertList] = useState([]);
  const [activeItem, setActiveItem] = useState(null);
  const [selectedAlert, setSelectedAlert] = useState(null);
  const [alertTypeQuery, setAlertTypeQuery] = useState(null);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1); // Pagination page state
  const listRef = useRef(null);
  const getAlertListData = useCallback(() => {
    setLoading(true);
    getAlertList({
      to_date: "2024-11-30",
      from_date: dayjs().format("YYYY-MM-DD"),
      is_resolved: false,
      limit: 20, // Limit to 10 alerts per page
      page,
      ...alertTypeQuery,
    })
      .then((res) => {
        setAlertList((prev) => [...prev, ...res.data]); // Append new data
        setLoading(false);
      })
      .catch((err) => {
        console.error(err);
        setLoading(false);
      });
  }, [alertTypeQuery, page]);

  useEffect(() => {
    getAlertListData();
  }, [page, alertTypeQuery]);

  useEffect(() => {
    if (alertList.length > 0 && !activeItem) {
      const firstAlert = alertList[0];
      setActiveItem(firstAlert._id);
      setSelectedAlert(firstAlert);
    }
  }, [alertList, activeItem]);

  const onChange = (key) => {
    setActiveItem(key);
    const selected = alertList.find((alert) => alert._id === key);
    setSelectedAlert(selected);
  };

  const handleScroll = () => {
    const { scrollTop, scrollHeight, clientHeight } = listRef.current;

    // Check if user has scrolled to the bottom
    if (scrollTop + clientHeight >= scrollHeight - 10 && !loading) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  const onSegmentChange = (value) => {
    setAlertTypeQuery(getAlertsGroup(value));
    setPage(1); // Reset to the first page
    setAlertList([]); // Clear the existing list
  };

  return (
    <div className="w-full flex justify-center items-center h-[495px] overflow-hidden">
      <div className="w-[30%] flex flex-col gap-4 h-full">
        <Segmented
          style={{ width: "100%" }}
          block
          options={segmentsForAlertView}
          onChange={(value) => onSegmentChange(value)}
        />
        <div
          ref={listRef}
          className="w-full overflow-auto"
          style={{ height: "440px" }}
          onScroll={handleScroll}
        >
          {alertList.map((alert) => (
            <div
              key={alert._id}
              onClick={() => onChange(alert._id)}
              className={`custom-tab-label flex justify-between items-center p-2.5 rounded-md cursor-pointer ${
                activeItem === alert._id ? "bg-gray-100" : ""
              }`}
            >
              <span className="text-black font-semibold">
                {alert.room_name}
              </span>
              <Space>
                {getAlertInfoViaEvent(alert)?.label === "Critical" && (
                  <Badge color="red" />
                )}
                {getAlertInfoViaEvent(alert)?.label === "Warning" && (
                  <Badge color="orange" />
                )}
                {getAlertInfoViaEvent(alert)?.label === "Info" && (
                  <Badge color="blue" />
                )}
              </Space>
            </div>
          ))}
          {loading && (
            <div className="w-full flex justify-center py-4">
              <Spin />
            </div>
          )}
          {!loading && alertList.length === 0 && (
            <Empty description="No active alerts available" />
          )}
        </div>
      </div>
      <div className="w-[70%] h-full">
        {selectedAlert ? (
          <RoomAndDetails data={selectedAlert} />
        ) : (
          <Empty description="Select an alert to view details" />
        )}
      </div>
    </div>
  );
}

export const segmentsForAlertView = [
  { label: "All", value: "All" },
  { label: "Critical", value: "Critical" },
  { label: "Warning", value: "Warning" },
  { label: "Info", value: "Info" },
];
